"use client";

const API_URL = process.env.NEXT_PUBLIC_API_URL || "http://127.0.0.1:8000";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import Chatbot from "../Chatbot";
import { useThemeAuth } from "../context/ThemeAuthContext";
import toast from "react-hot-toast";

// ƒ∞KONLAR
const MoonIcon = () => (<svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" strokeWidth={2}/></svg>);
const SunIcon = () => (<svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" strokeWidth={2}/></svg>);

export default function PlannerPage() {
  const { user, darkMode, toggleTheme, logout } = useThemeAuth();
  const router = useRouter();
  
  // --- STATE Y√ñNETƒ∞Mƒ∞ ---
  const [step, setStep] = useState(1);
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({
    idea: "",
    capital: "",
    skills: "",
    strategy: "",
    management: "",
    language: "tr"
  });

  // G√ºvenlik Kontrol√º
  useEffect(() => {
    if (!localStorage.getItem("token")) router.push("/login");
  }, [router]);

  // --- SORULAR Lƒ∞STESƒ∞ ---
  const questions = [
    { id: 1, key: "idea", title: "ƒ∞≈ü Fikriniz Nedir?", ph: "√ñrn: Kadƒ±k√∂y'de plak konseptli 3. dalga kahve d√ºkkanƒ±..." },
    { id: 2, key: "capital", title: "Mevcut Kaynaklar (Sermaye)", ph: "√ñrn: 500.000 TL nakit, 2 ortak..." },
    { id: 3, key: "skills", title: "Yetenekler & G√º√ßl√º Y√∂nler", ph: "√ñrn: 5 yƒ±l barista tecr√ºbesi, sosyal medya y√∂netimi..." },
    { id: 4, key: "strategy", title: "Gelecek Hedefleri", ph: "√ñrn: 1 yƒ±l i√ßinde ≈üubele≈ümek ve kendi kahvemi kavurmak..." },
    { id: 5, key: "management", title: "Y√∂netim Ekibi", ph: "√ñrn: Ben ve finans i≈ülerine bakan ortaƒüƒ±m..." }
  ];

  // --- AKILLI DOƒûRULAMA (VALIDATION) ---
  const validateInput = (key: string, value: string) => {
    const val = value.trim();

    // 1. Bo≈ü kontrol√º
    if (!val) return "L√ºtfen bu alanƒ± bo≈ü bƒ±rakmayƒ±n!";

    // 2. SERMAYE (CAPITAL) ALANI ƒ∞√áƒ∞N √ñZEL KURAL
    if (key === "capital") {
        if (val.length < 2) return "L√ºtfen ge√ßerli bir miktar girin.";
        return null; 
    }

    // 3. GELƒ∞≈ûMƒ∞≈û "ANLAMSIZ YAZI" KONTROL√ú
    if (val.length < 10) return "L√ºtfen fikrinizi biraz daha detaylandƒ±rƒ±n (En az 10 karakter).";

    // KURAL A: Aynƒ± harfin 4 kereden fazla pe≈ü pe≈üe gelmesi (aaaaa)
    const repetitiveCharRegex = /(.)\1{4,}/;
    
    // KURAL B: √áok uzun kelime (sdfsdfsdfdsgdsgf...)
    const longWordRegex = /[^\s]{20,}/;

    // KURAL C: Pe≈ü pe≈üe √ßok fazla sessiz harf (√ñrn: "hgfhgf" 6 sessiz harf yan yana)
    // T√ºrk√ßede en fazla 3 sessiz harf yan yana gelebilir (istisnalar hari√ß), 5+ kesin anlamsƒ±zdƒ±r.
    const excessiveConsonantsRegex = /[^aeƒ±io√∂u√º\s\d,.\-]{5,}/i;

    // KURAL D: Sesli harf oranƒ± kontrol√º
    const vowelCount = (val.match(/[aeƒ±io√∂u√ºAEIƒ∞O√ñU√ú]/g) || []).length;
    const vowelRatio = vowelCount / val.replace(/\s/g, '').length;

    if (repetitiveCharRegex.test(val) || longWordRegex.test(val) || excessiveConsonantsRegex.test(val)) {
        return "L√ºtfen anlamlƒ± kelimeler kullanƒ±n. Rastgele tu≈ülara basmayƒ±n.";
    }

    // Eƒüer √ßok kƒ±saysa veya sesli harf hi√ß yoksa (Sermaye hari√ß)
    if (vowelCount === 0) {
        return "L√ºtfen i√ßinde sesli harf olan anlamlƒ± bir c√ºmle kurun.";
    }

    return null;
  };

  // --- ƒ∞LERLEME FONKSƒ∞YONU ---
  const handleNext = () => {
    const currentKey = questions[step - 1].key as keyof typeof formData;
    const currentValue = formData[currentKey];

    const errorMsg = validateInput(currentKey, currentValue);
    if (errorMsg) {
        alert(errorMsg); 
        return;
    }
    
    if (step < 5) {
        setStep(step + 1);
    } else {
        generatePlan();
    }
  };

  // --- ENTER TU≈ûU KONTROL√ú ---
  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === "Enter" && !e.shiftKey) {
        e.preventDefault();
        handleNext();
    }
  };

  // --- PLAN OLU≈ûTURMA VE ƒ∞NDƒ∞RME ---
  const generatePlan = async () => {
    setLoading(true);
    const loadingId = toast.loading("Plan olu≈üturuluyor...");
    try {
      const res = await fetch(`${API_URL}/generate_plan`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });

      if (!res.ok) throw new Error("Plan olu≈üturulamadƒ±.");

      const blob = await res.blob();
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = "StartERA_Plan.pdf";
      document.body.appendChild(a);
      a.click();
      a.remove();
      
      toast.success("Plan indirildi!");
      
      setStep(1); 
      setFormData({ idea: "", capital: "", skills: "", strategy: "", management: "", language: "tr" });

    } catch (error) {
      toast.error("Sunucuyla baƒülantƒ± kurulamadƒ±. L√ºtfen tekrar deneyin.");
    } finally {
      setLoading(false);
      toast.dismiss(loadingId);
    }
  };

  if (!user) return null;

  return (
    <div className={`min-h-screen flex flex-col transition-colors duration-500 ${darkMode ? "bg-slate-900 text-white" : "bg-slate-50 text-slate-900"}`}>
      
      <Chatbot lang="tr" darkMode={darkMode} />

      <nav className={`p-4 border-b flex justify-between items-center ${darkMode ? "bg-slate-900 border-slate-700" : "bg-white border-slate-200"}`}>
        <Link href="/dashboard" className="font-black text-2xl text-blue-600 hover:opacity-80 transition">Start ERA</Link>
        <div className="flex items-center gap-4">
             <button onClick={toggleTheme} className={`p-2 rounded-full transition ${darkMode ? 'bg-slate-800 text-yellow-400' : 'bg-white text-slate-600 shadow-sm'}`}>
                {darkMode ? <SunIcon /> : <MoonIcon />}
             </button>
             <Link href="/dashboard" className={`px-4 py-2 rounded-lg font-bold text-sm border transition ${darkMode ? "border-slate-700 hover:bg-slate-800" : "border-slate-200 hover:bg-slate-100"}`}>
                ‚Üê Vazge√ß
             </Link>
        </div>
      </nav>

      <div className="flex-1 flex flex-col items-center justify-center p-6 w-full max-w-2xl mx-auto">
        
        <div className="w-full bg-gray-200 rounded-full h-2.5 mb-10 dark:bg-gray-700 overflow-hidden">
            <div className="bg-blue-600 h-2.5 rounded-full transition-all duration-500 ease-out" style={{ width: `${(step / 5) * 100}%` }}></div>
        </div>

        <div className={`w-full p-10 rounded-3xl shadow-2xl border transition-all ${darkMode ? "bg-slate-800 border-slate-700" : "bg-white border-slate-100"}`}>
            
            <h2 className="text-3xl font-black mb-2 animate-in fade-in slide-in-from-bottom-2">
                {questions[step - 1].title}
            </h2>
            <p className={`mb-6 text-sm font-medium ${darkMode ? "text-slate-400" : "text-slate-500"}`}>
                Adƒ±m {step} / 5
            </p>

            <textarea 
                rows={5}
                className={`w-full p-5 rounded-2xl border-2 outline-none focus:ring-4 transition-all text-lg mb-8 resize-none ${darkMode ? "bg-slate-900 border-slate-700 focus:border-blue-600 focus:ring-blue-900/50 text-white placeholder:text-slate-600" : "bg-slate-50 border-slate-200 focus:border-blue-500 focus:ring-blue-100 text-slate-900 placeholder:text-slate-400"}`}
                placeholder={questions[step - 1].ph}
                value={formData[questions[step - 1].key as keyof typeof formData]}
                onChange={(e) => setFormData({...formData, [questions[step - 1].key]: e.target.value})}
                onKeyDown={handleKeyDown}
                autoFocus
            />

            <div className="flex justify-between items-center">
                {step > 1 ? (
                    <button onClick={() => setStep(step - 1)} className="px-6 py-3 font-bold text-slate-500 hover:text-slate-700 transition">
                        Geri
                    </button>
                ) : <div></div>}

                <button 
                    onClick={handleNext} 
                    disabled={loading}
                    className={`px-8 py-4 rounded-xl font-bold text-white shadow-xl transition-all transform active:scale-95 disabled:opacity-70 disabled:cursor-not-allowed ${loading ? 'bg-slate-600' : 'bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700'}`}
                >
                    {loading ? (
                        <span className="flex items-center gap-2">
                           <svg className="animate-spin h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"><circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle><path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path></svg>
                           Plan Yazƒ±lƒ±yor...
                        </span>
                    ) : (step === 5 ? "Planƒ± Olu≈ütur ‚ú®" : "Devam Et ‚Üí")}
                </button>
            </div>

        </div>
      </div>
    </div>
  );
}
